@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;700;800&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Epilogue&display=swap');

*,
html {
  margin: 0;
  padding: 0;
  font-family: 'Epilogue', sans-serif;
  scroll-behavior: smooth;
  box-sizing: border-box;
}

body::-webkit-scrollbar {
  width: 10px;
  height: 0px;
  background-color: rgb(181, 185, 189);
}

body::-webkit-scrollbar-track {
  box-shadow: inset aliceblue;
}

body::-webkit-scrollbar-thumb {
  background: grey;
  border-radius: 0;
}


body {
  color:#333;
  overflow-x: hidden;
  transition: background-color 3s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  background-color: aliceblue;
  user-select: none;
}

.body2 {
  color: aliceblue;
  overflow-x: hidden;
  background-color: rgb(47, 42, 42);
  user-select: none;
}

.container {
  position: fixed;
  backdrop-filter: blur(20px);
  align-items: center;
  border-color: gray;
  height: 40px;
  width: 100%;
  top: 0;
  /* box-shadow: 0 2px 4px 0 rgba(0, 0, 0, .2); */
  z-index: 4;
  border-bottom: solid 1px gray;
}

.bg--nav {
  height: 42px;
  background-color: gray;
  width: 100%;
}

li {
  list-style: none;
}

.text {
  margin-top: 4.5px;
  position: relative;
  font-size: 1em;
}

#berandanav {
  color: aliceblue;
  text-decoration: none;
  text-shadow: 0 0 3px gray, 0 0 5px gray;
  font-family: 'Poppins', sans-serif;
}

.text:hover {
  filter: brightness(50%);
  transition: filter ease-in 0.3s;
}

.style {
  align-items: center;
  position: relative;
  display: flex;
  gap: 30px;
  padding-right: 5px;
}

.bg {

  background-color: gray;
  height: 40px;
  width: 100%;
  opacity: 80%;
  position: fixed;
}

.logo {
  transition: filter 3s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  margin-top: 4.5px;
  opacity: 90%;
  margin-left: 15px;
  /* border: 1px solid gray; */
  border-radius: 5px;
  height: 30px;
  width: 30px;
  background-repeat: no-repeat;
  background-image: url(https://riztranslation.dee.al/assets/Icon/rizlogo.svg);
  background-size: cover;
}

.toggle {
  position: absolute;
  opacity: 0%;
  margin-top: 5px;
  margin-right: 5px;
  transform: translate(15px);
  border: none;
  border-radius: 5px;
  width: 30px;
  height: 30px;
  display: inline-block;
  background-color: rgb(47, 42, 42);
  cursor: pointer;
  color: aliceblue;
}

.search {
  background: transparent;
  border: none;
  border-bottom: solid 1px aliceblue;
  height: 20px;
  box-sizing: border-box;
  display: inline-block;
  padding: 10px;
  font-family: 'Poppins', sans-serif;
  color: aliceblue;
}

.search::placeholder {
  color: aliceblue;
}

.search:focus::placeholder {
  color: rgb(167, 170, 173);
  transition: color 0.6s linear;
}

.search:focus {
  transition: border-bottom 0.6s linear;
  outline: none;
  border-bottom: solid 1px rgb(167, 170, 173);
}

input::-webkit-calendar-picker-indicator {
  opacity: 0%;
}

.main {
  height: fit-content;
  display: grid;
  padding: 5px;
  grid-template-areas:
    'header detail'
    'midle midle'
    'footer footer';
  color: inherit;
}

.main2 {
  display: grid;
  padding: 5px;
  grid-template-areas:
    'header detail'
    'midle midle'
    'footer footer';
  color: aliceblue;
}

.sampul {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 10px;
  grid-area: header;
}

.sampul img {
  height: 300px;
  border-radius: 5px;

}

.detail {
  color: inherit;
  margin: 5px;
  grid-area: detail;
  height: fit-content;
}

.detail h3, h5 {
  color: inherit;
  margin: inherit;
  font-family: 'Poppins', sans-serif;
}

h3 {
  font-weight: 700;
}

h5 {
  border-bottom: 1px solid rgb(194, 203, 210);
  height: 20px;
  font-weight: 300;
}

.detail a {
  display: inline-block;
  align-items: center;
  font-size: 10px;
  font-family: 'poppins', sans-serif;
  color: aliceblue;
  background-color: gray;
  text-decoration: none;
  height: 20px;
  text-align: center;
  padding: 3px;
  padding-top: 1.5px;
  border: 1px solid gray;
  border-radius: 5px;
  margin: 3px;
}

.status {
  display: inline-block;
  font-size: 10px;
  color: inherit;
  text-decoration: none;
  height: 15px;
  text-align: center;
  margin: 3px;
  font-family: 'Poppins', sans-serif;
  font-weight: 700;
}
.detail section {
  margin-top: 10px;
}
.deskripsi{
  color: inherit;
}

.deskripsi p {
  font-size: 0.8em;
  text-overflow: ellipsis;
  display: inline-block;
  line-height: 1.3em;
  overflow: scroll;
  width: 95%;
  height: 130px;
  transform: translate(-5px);
  color: inherit;
  font-family: 'Poppins', sans-serif;
}

.deskripsi p::-webkit-scrollbar {
  width: 2px;
  height: 0px;
  opacity: 50%;
}

.deskripsi p::-webkit-scrollbar-track {
  box-shadow: inset aliceblue;
}

.deskripsi p::-webkit-scrollbar-thumb {
  background: grey;
  border-radius: 10px;
}

.deskripsi {
  margin: 10px;
  height: 70px;
  width: 100%;
}
.header {
  transition: color 3s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  margin: 5px;
  font-family: 'Poppins', sans-serif;
  font-size: medium;
  font-weight: 500;
  color: inherit;
  height: 25px;
  border-bottom: 1px solid rgb(194, 203, 210);
}

.chapter {
  grid-area: midle;
  color: gray;
}

.chapter2 {
  grid-area: midle;
  color: aliceblue;
}
.borderbtm {
  transition: color 3s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  margin: 3px;
  font-family: 'Poppins', sans-serif;
  font-size: medium;
  font-weight: 500;
  color: inherit;
  height: 3px;
  border-bottom: 1px solid rgb(194, 203, 210);
}
.link{
  margin: 10px;
  text-decoration: none;
  color: inherit;
  font-family: 'poppins', sans-serif;
}

.chapter{
  color: inherit;
}
.active{
  transition:3s step-start;
  border-bottom: 3px solid gray;
}
.conNav{
  display: flex;
  padding: 3px;
}

@media only screen and (max-width: 700px) {
  .main {
    grid-template-areas:
      'header'
      'midle'
      'footer';
  }

  .detail {
    grid-area: midle;
  }

  .chapter {
    grid-area: footer;
  }

  .header {
    margin-top: 50px;
  }

  .search {
    width: 50%;
  }
}