@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;700;800&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Epilogue&display=swap');


.conCh {
    display: flex;
    margin-bottom: 10px;
    animation: fadeIn 0.5s;
}

.conCh p {
    font-family: 'poppins', sans-serif;
    color: inherit;
    font-size: 0.7em;
}

.timeago {
    font-family: 'poppins', sans-serif;
}

.conCh h3 {
    font-family: 'poppins', sans-serif;
    font-weight: lighter;
}

.chapterLink {
    color: inherit;
    text-decoration: none;
}

.sampulCh {
    height: 50px;
    width: 30px;
    margin-right: 10px;
    background-size: 300%;
    background-position: center;
    border: 1px solid gray;
}

.vol1 {
    background-image: url(https://pbs.twimg.com/media/FNuKuMlagAMXAVR?format=webp&name=360x360);
}

.vol2 {
    background-image: url(https://pbs.twimg.com/media/FaHhTVTUUAE-lFa?format=webp&name=360x360);
}

::-webkit-scrollbar {
    width: 0px;
    height: 0px;
    background-color: rgb(181, 185, 189);
}


::-webkit-scrollbar-track {
    box-shadow: inset aliceblue;
}

::-webkit-scrollbar-thumb {
    background: grey;
    border-radius: 0;
}

@-webkit-keyframes fadeIn {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}

@keyframes fadeIn {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}